
export default {
	onLaunch: function() {
		console.log('App Launch')
		let idx = uni.getStorageSync('SKIN_INDEX')
		if(idx) {
			this.$store.state.skinIndex = idx
			this.$store.state.skinList.forEach((item, index) => {
				item.selected = false
				if(index === idx) {
					item.selected = true
				}
			})
		}
	},
	onShow: function() {
		console.log('App Show')
	},
	onHide: function() {
		console.log('App Hide')
	}
}
